@import url('https://fonts.googleapis.com/css?family=Bai+Jamjuree:400,400i,700,700i&display=swap&subset=vietnamese');

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 50px;
    display: block;
    color: #000;
    font-weight: 400;
    font: 16px/1.5 'Bai Jamjuree', "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    background-color: #fff;
    background-image: url('/assets/img/light-wool.png');   
    background-attachment: scroll;
}

nav {
    display: flex;
}

nav .view {
    font-size: 16px;
    padding-right: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #222;
    margin: 0 0 20px
}

p,
ul,
ol,
// table,
dl {
    margin: 0 0 20px
}

h1,
h2,
h3 {
    line-height: 1.1
}

h1 {
    font-size: 28px
}

h2 {
    color: #393939
}

h3,
h4,
h5,
h6 {
    color: #494949
}

a {
    color: #267CB9;
    text-decoration: none
}

a:hover,
a:focus {
    color: #069;
    font-weight: bold
}

a small {
    font-size: 11px;
    color: #777;
    margin-top: -0.3em;
    display: block
}

a:hover small {
    color: #777
}

.wrapper {
    width: 860px;
    margin: 0 auto
}

blockquote {
    opacity: 0.75;
    border-left: 2px solid #e5e5e5;
    margin: 0;
    padding: 0 0 0 20px;
    font-style: italic
}

button {
    border: none;
    outline: none;
}

code,
pre {
    font-family: Monaco, Liberation Mono, monospace;
    color: #333;
    font-size: 14px;
}

a code,
p code,
table td code,
li code {
	color: #0009;
    background: #4444661a;
    padding: 1px 5px;
    border-radius: 2px;
    word-wrap: break-word;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
}

table {
    width: 100%;
    border-collapse: collapse
}

th,
td {
    text-align: left;
    padding: 5px 10px;
    /* border-bottom: 1px solid #e5e5e5 */
}

dt {
    color: #444;
    font-weight: 700
}

th {
    color: #444
}

img {
    max-width: 100%
}

header {
    width: 270px;
    float: left;
    position: fixed;
    -webkit-font-smoothing: subpixel-antialiased
}


strong {
    color: #222;
    font-weight: 700
}

a strong {
    color: #267CB9;
}

header ul li+li+li {
    border-right: none;
    width: 89px
}

header ul a strong {
    font-size: 14px;
    display: block;
    color: #222
}

section {
    width: 580px;
    float: right;
    padding-bottom: 50px
}

small {
    font-size: 11px
}

hr {
    border: 0;
    background: #e5e5e5;
    height: 1px;
    margin: 0 0 20px
}

footer {
    width: 270px;
    float: left;
    position: fixed;
    bottom: 50px;
    -webkit-font-smoothing: subpixel-antialiased
}

a:hover,
a:focus {
    color: #069;
    font-weight: normal;
    text-decoration: underline
}

table.image td {
    text-align: center
}

button.next {
    float: right
}

.list-post h2 {
    font-size: 18px;
}

.list-post > ul {
    font-size: 18px;
    padding-left: 25px;
}

.list-post ul > p {
    margin-bottom: 5px;
    font-size: 20px;
}

.list-post ul p > i {
    float: right;
    font-size: 14px;
    opacity: 0.75;
    margin-right: 15px;
    margin-top: 5px;
}

.list-post ul p > a {
    font-weight: 600;
}

.list-post br {
    display: none;
}

.post-title {
    margin-bottom: 5px;
}

.post-date {
    font-style: italic;
}

.post-toc p {
    margin: 0;
    font-size: 13px;
}

.post-content {
    position: relative;
    padding-bottom: 40px;
}

.post-content h1,
.post-content h2,
.post-content h3 {
    padding-top: 10px;
}


pre {
    font: normal 13px/20px "Source Code Pro", Menlo, Consolas, Monaco, monospace;
    padding: 0;
    margin: 0;
}

.source-file,
.source-file-narrow {
    font: normal 11px/16px "Source Code Pro", Menlo, Consolas, Monaco, monospace;
    color: #bab8b7;
}

.source-file em,
.source-file-narrow em {
    color: #999997;
    font-style: normal;
}

.source-file-narrow {
    display: none;
    margin: -30px -12px 15px 0;
    padding: 15px 0 0 0;
    text-align: right;
}

.source-file {
    position: absolute;
    right: -185px;
    width: 150px;
    padding: 5px 0 0 0;
}
.source-file p{
    margin: 0;
    padding: 0;
}

.source-file::before {
    content: "<<";
    color: #dad8d6;
    position: absolute;
    left: -30px;
    width: 36px;
    text-align: center;
}

.c {
    text-align: center;
}

.l {
    text-align: left;
}

.r {
    text-align: right;
}

.scroll-top {
    border-radius: 4px;
    width: 40px;
    height: 40px;
    background-color: #fff;
    display: none;
    position: fixed;
    bottom: 30px;
    right: 30px; 
    z-index: 9999;
    box-shadow: 0 1px 3px #1a1a1a1a;
    text-align: center;
    padding: 0;
    line-height: inherit;
}

.scroll-top svg {
    width: 24;
    height: 24;
    margin-top: 3px;
}

.scroll-top:hover {
    background: #ccc5;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
    background: #eee; 
}

::-webkit-scrollbar-thumb {
    background: #999; 
}

::-webkit-scrollbar-thumb:hover {
    background: #666;
}

::-moz-selection {
    background: #d5d5d5;
}

::selection {
    background: #d5d5d5;
}

@media only screen and (max-width: 1240px) {
    header, footer {
        left: 10%;
    }
    .source-file-narrow {
        display: block;
    }
    .source-file {
        display: none;
    }
    #post-title br {
        display: none;
    }
}

@media print,
screen and (max-width: 960px) {
    div.wrapper {
        width: auto;
        margin: 0 5%;
    }
    header,
    section,
    footer {
        float: none;
        position: static;
        width: auto;
    }
    header, footer {
        padding-right: 240px
    }
    section {
        border: 1px solid #e5e5e5;
        border-width: 1px 0;
        padding: 20px 0;
        margin: 0 0 20px;
    }
    header a small {
        display: inline
    }
    header ul {
        position: absolute;
        right: 50px;
        top: 52px;
    }
    img {
        width: 90%;
        height: 90%;
    }
}

@media print,
screen and (max-width: 720px) {
    body {
        word-wrap: break-word
    }
    header {
        padding: 0
    }
    header ul,
    header p.view {
        position: static
    }
    header, footer {
        padding-right: 50px
    }
    pre,
    code {
        word-wrap: normal
    }
}

@media print,
screen and (max-width: 480px) {
    body {
        padding: 15px
    }
    header ul {
        width: 99%
    }
    header li,
    header ul li+li+li {
        width: 33%
    }
}
