code.language-insert-boder {
    margin: 0px;
    padding: 8px 12px;
    padding-top: 5px;
    border-left: solid 2px #32a852;
    border-right: solid 2px #32a852;
    background: #f5f3f0;
    display: inline-block;
    min-width: 100%;
    color:#585d5b;
    font-size: small;
}

code.language-insert {
    margin: 0px;
    padding: 8px 0;
    background: #f5f3f0;
    display: inline-block;
    min-width: 100%;
    color:#585d5b;
    font-size: small;
}

@import 'theme/monokai.scss';


.language-bash {
