.search-results-ul{
    top: 1px;
    border-radius: 0;
    /* display: none; */
}

#search-input {
    border-top: 0;
    border-right: 0;
    border-left: 0;
    border-radius: 0;
    min-width: 280px;
}
#search-input:focus {
    box-shadow: none;
    border-bottom: solid 1px #00b571;
}
    
.search-results ul li:not(:last-child) {
    margin-bottom: 10px;
}

.search-results ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.search-results ul li {
    padding: 5px 20px;
 }
 
 .search-results ul li p {
    font-size: .9em;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
}
.search-results ul li a{
    display: block;
    width: 100%;
}

.search-results ul li span {
    font-size: .8em;
    font-weight: 300;
    color: #adb5bd;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
}
#toc{
    max-height: 55vh;
    overflow: auto;
    padding: 2px 0 40px 0;
}
ol.post-toc{
    margin: 0;
    padding: 0 0 0 16px;
    list-style-type: upper-roman;
    ol.post-toc{
        list-style-type: decimal;  
        ol.post-toc{
            list-style-type: lower-alpha;  
        }
    }
}
.nav-tab-Prev-Next-post{
    clear: both;
    .btn-nav{
        border: 1px dotted;
        padding: 2px 5px;
        border-radius: 10px;
    }
}

.bai-viet-tuong-tu{
    margin-top: 20px;
    border-top: solid 1px #c1b0b0;
    padding-top: 10px;
    
    .related_posts{
        clear: both;
    }
}
